<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <el-button type="primary" icon="el-icon-plus" size="small" @click="addCoursePack">新增课程包</el-button>
            <div>
                <SelectTags v-model="labelList" />
                <el-input
                        placeholder="请输入课程包名称或课程包ID"
                        size="small"
                        style="width: 220px"
                        v-model="searchParams.keyWord"
                        clearable></el-input>
                <el-select v-model="searchParams.sellType" size="small" clearable placeholder="售卖类型"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.limtFree" size="small" clearable placeholder="限时免费"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.discountType" size="small" clearable placeholder="折扣类型"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <Operator v-model="searchParams.operator"></Operator>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="课程包名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="运营标签">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="售卖类型">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="售卖金额">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="折扣类型">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="最近操作人">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="最近操作人">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="发布时间">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="原子课程数量">
            </el-table-column>
            <el-table-column
                    prop="label"
                    label="状态">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="210">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isopen" @click="enable(scope.row)" type="success" size="small"
                               >启用</el-button>
                    <el-button v-else @click="forbidden(scope.row)" type="primary" size="small"
                               >禁用</el-button>
                    <el-button @click="toDetail(scope.row)" type="info" size="small"
                    >预览</el-button>
                    <el-button @click="editorFn(scope.row)" type="danger" size="small"
                               >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                labelList: [],
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: true,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: false,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            addCoursePack() {
                this.$router.push('/course_pack_add_3')
            },
            toDetail() {
                this.$router.push('/course_pack_detail_3')
            },
            editorFn() {
                this.$router.push('/course_pack_editor_3')
            },
            forbidden(row) {
                this.$confirm('确定禁用该课程包?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该课程包?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用'
                    });
                });
            }
        },

        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, SelectTags
        }
    }
</script>
